.language-picker {\n  position: relative;\n  margin: 4px 8px 0 0;\n\n  .current {\n    cursor: pointer;\n  }\n\n  ul {\n    display: none;\n    background: #f5f5f6;\n    border: none;\n    margin: 0;\n    padding: 0;\n    overflow: hidden;\n    border-radius: 3px 0 3px 3px;\n    background-clip: padding-box;\n    margin-bottom: 5px;\n    position: absolute;\n    top: 100%;\n    right: 0;\n\n    li {\n      cursor: pointer;\n      border-bottom: 1px solid #ebebeb;\n      padding: 10px 20px;\n      margin: 0;\n      outline: 0;\n      color: #7d8086;\n      display: flex;\n      flex-direction: row;\n      align-items: center;\n\n      span {\n        padding-left: 8px;\n      }\n\n      &:hover {\n        background: rgba(235, 235, 235, 0.9);\n        color: #737881;\n      }\n    }\n\n    &.open {\n      display: block;\n    }\n  }\n}\n\n.language-icon {\n  height: 16px;\n  width: 16px;\n}\n\n@keyframes fade-in {\n  from {\n    opacity: 0;\n  }\n  to {\n    opacity: 1;\n  }\n}\n\n.app-container {\n  flex: 1;\n  position: relative;\n}\n\n.pi-notifications {\n  position: fixed;\n  display: flex;\n  align-items: flex-end;\n  flex-direction: column;\n  right: 2em;\n  bottom: 2em;\n\n  .pi-item {\n    box-sizing: border-box;\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    text-align: left;\n    padding: 0.8em;\n    background-color: $bg-color;\n    max-width: 500px;\n    position: relative;\n    box-shadow: 1px 7px 14px -5px rgba(0,0,0,0.2);\n\n    &+& {\n      margin-top: 1em;\n    }\n\n    &.success:before {\n      background-color: #2BDE3F;\n    }\n\n    &.error:before {\n      background-color: #e7431a;\n    }\n\n    &.info:before {\n      background-color: #1D72F3;\n    }\n\n    &.warning:before {\n      background-color: #FFC007;\n    }\n\n    &:before {\n      content: '';\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 2px;\n    }\n\n    .pi-details {\n      flex: 1;\n\n      .pi-title {\n        color: #3e3e3e;\n        font-weight: 700;\n        margin-top: 0;\n        margin-bottom: 8px;\n      }\n\n      .pi-description {\n        font-size: 14px;\n        margin-top: 0;\n        margin-bottom: 0;\n        color: #878787;\n      }\n    }\n\n    .pi-close {\n      width: 14px;\n      cursor: pointer;\n      height: 14px;\n      fill: #878787;\n      margin-left: 1.2em;\n      background: url('../images/close.svg') no-repeat center center;\n    }\n  }\n\n  @media screen and (max-width: $breakpoint-tablet) {\n    right: 0;\n    left: 0;\n    bottom: 0;\n\n    .pi-item {\n      max-width: 100%;\n      width: 100%;\n    }\n  }\n}\n\n.app-container, #app {\n  display: flex;\n  flex-direction: column;\n}\n\n.app-content {\n  display: flex;\n  flex-direction: column;\n  flex-grow: 1;\n  padding: 0 2em;\n}\n\n.app-title {\n  display: flex;\n  position: relative;\n  align-items: center;\n}\n\n.app-header {\n  display: flex;\n  flex-direction: row;\n  padding: 0 2em;\n  margin-bottom: 1em;\n  align-items: center;\n  justify-content: space-between;\n  background: $header-color;\n  position: relative;\n  z-index: 2;\n\n  h1 {\n    white-space: nowrap;\n    font-size: 2rem;\n  }\n\n  @media screen and (max-width: $breakpoint-tablet) {\n    padding: 0 1em;\n\n    h1 {\n      display: none;\n    }\n  }\n}\n\n.pi-center {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  flex-grow: 1;\n}\n\n.app-footer {\n  font-size: 0.8em;\n  padding: 1em 0;\n  background: $footer-color;\n  display: flex;\n  flex-direction: row;\n\n  .pi-menu {\n    margin: 0 auto;\n    max-width: 1000px;\n    column-width: 300px;\n    display: block;\n\n    .pi-item {\n      display: block;\n      padding: 1em 2em;\n    }\n  }\n}\n\n.app-menu {\n  position: fixed;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  padding: 0;\n  margin: 0;\n  width: 300px;\n  max-width: 100%;\n  background: lighten($header-color, 15%);\n  transform: translateX(-100%);\n  transition: all 0.1s ease;\n  padding-top: 85px;\n  z-index: 1;\n\n  &.is-open {\n    transform: none;\n    box-shadow: $menu-shadow-size 0px $menu-shadow-size (-$menu-shadow-size) rgba(0, 0, 0, 0.2);\n  }\n\n  .app-menu-content {\n    overflow-y: auto;\n    height: 100%;\n  }\n\n  .pi-menu {\n    display: flex;\n    flex-direction: column;\n    list-style: none;\n\n    > .pi-item {\n      flex: 1;\n      margin: 1px 0;\n\n      > a {\n        display: block;\n        border-left: 5px solid $header-color;\n        padding: 1em;\n\n        &:hover {\n          border-left-color: $menu-color;\n        }\n      }\n    }\n  }\n}\n\n.pi-spinner {\n  position: relative;\n  width: $basic-spinner-dimensions;\n  height: $basic-spinner-dimensions;\n  font-size: 0;\n\n  &:before,\n  &:after {\n    content: \"\";\n    display: block;\n    position: absolute;\n    border-width: 4px;\n    border-style: solid;\n    border-radius: 50%;\n  }\n\n  @keyframes rotate-animation {\n    0% {\n      transform: rotate(0deg);\n    }\n\n    100% {\n      transform: rotate(360deg);\n    }\n  }\n\n  @keyframes anti-rotate-animation {\n    0% {\n      transform: rotate(0deg);\n    }\n\n    100% {\n      transform: rotate(-360deg);\n    }\n  }\n\n  &:before {\n    width: $main-spinner-dimensions;\n    height: $main-spinner-dimensions;\n    border-bottom-color: $basic-dark-color;\n    border-right-color: $basic-dark-color;\n    border-top-color: rgba($basic-dark-color, 0);\n    border-left-color: rgba($basic-dark-color, 0);\n    top: 0;\n    left: 0;\n    animation: rotate-animation 1s linear 0s infinite;\n  }\n\n  &:after {\n    width: $small-spinner-dimensions;\n    height: $small-spinner-dimensions;\n    border-bottom-color: $basic-dark-color;\n    border-right-color: $basic-dark-color;\n    border-top-color: rgba($basic-dark-color, 0);\n    border-left-color: rgba($basic-dark-color, 0);\n    top: math.div($main-spinner-dimensions - $small-spinner-dimensions, 2);\n    left: math.div($main-spinner-dimensions - $small-spinner-dimensions, 2);\n    animation: anti-rotate-animation 0.85s linear 0s infinite;\n  }\n}\n\n$tile-height: 70px;\n$tile-width: 70px;\n$tile-gap: 10px;\n\n.pi-content {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n}\n\n.pi-dashboard {\n  flex: 1;\n  position: relative;\n  display: grid;\n  grid-template-columns: repeat(auto-fit, $tile-width);\n  grid-template-rows: repeat(auto-fit, $tile-height);\n  grid-gap: $tile-gap;\n  overflow: visible;\n\n  .pi-tile {\n    display: flex;\n    position: relative;\n\n    &.small {\n      grid-column: span 1;\n      grid-row: span 1;\n      width: $tile-width;\n      height: $tile-height;\n    }\n\n    &.medium {\n      grid-column: span 2;\n      grid-row: span 2;\n      width: 2 * $tile-width + 1 * $tile-gap;\n      height: 2 * $tile-height + 1 * $tile-gap;\n    }\n\n    &.wide {\n      grid-column: span 4;\n      grid-row: span 2;\n      width: 4 * $tile-width + 3 * $tile-gap;\n      height: 2 * $tile-height + 1 * $tile-gap;\n    }\n\n    &.large {\n      grid-column: span 4;\n      grid-row: span 4;\n      width: 4 * $tile-width + 3 * $tile-gap;\n      height: 4 * $tile-height + 3 * $tile-gap;\n    }\n\n    &:hover {\n      outline: rgba(29, 29, 29, 0.1) solid 4px;\n    }\n  }\n}\n\n.pi-search {\n  position: relative;\n  flex: 1;\n  display: flex;\n  justify-content: center;\n\n  input[type=search] {\n    font-size: 1em;\n    border: 1px solid transparent;\n    background-color: #f1f1f1;\n    padding: 10px;\n    font-size: 16px;\n    max-width: 80%;\n    width: 250px;\n    box-sizing: border-box;\n    transition: width ease 0.1s;\n\n    @media screen and (max-width: $breakpoint-mobile) {\n      width: 100px;\n    }\n  }\n\n  &:focus-within input[type=search] {\n    width: 90%;\n    max-width: 90%;\n\n    & + .pi-details:not(:empty) {\n      display: block;\n      opacity: 1;\n    }\n  }\n\n  .pi-details {\n    display: none;\n    opacity: 0;\n    position: absolute;\n    z-index: 2;\n    top: 100%;\n    width: 90